$breakpoints: (
  "SMALL": (
    min-width: 768px,
  ),
  "MEDIUM": (
    min-width: 960px,
  ),
  "LARGE": (
    min-width: 1120px,
  ),
  "XLARGE": (
    min-width: 1440px,
  ),
  "XXLARGE": (
    min-width: 2000px,
  ),
);

@mixin respond-to($breakpoint) {
  $raw-query: map-get($breakpoints, $breakpoint);

  @if $raw-query {
    $query: if(
      type-of($raw-query) == "string",
      unquote($raw-query),
      inspect($raw-query)
    );

    @media #{$query} {
      @content;
    }
  } @else {
    @error "No value found for `#{$breakpoint}`."
      + "Please make sure it is defined in `$breakpoints` map.";
  }
}
